/* eslint-disable quotes */
import React from 'react'
import IFeaturedWork from '../../../../../../../../interfaces/IFeaturedWork.interface'
import random from '../../../../../../../../utils/random'

import styles from './styles/_featured-work.module.scss'

function FeaturedWork (props: {index: number, work: IFeaturedWork}) {
	const { index, work } = props

	function getBackgroundImage (index: number) {		
		try {
			if (index > 11) index = index - 11
			const imgURL: string = `url(${require(`../../../../../../../../assets/images/background/background_${index}.jpg`)})`
			const backgroundImgCSS = {
				backgroundImage: imgURL
			}
			return backgroundImgCSS
		} catch (error) {
			console.log(error)
			return {}
		}
	}

	const backgroundImage = getBackgroundImage(index + 1)

	return (
		<div className={styles['featured-work']} style={{ ...backgroundImage }}>
			<div className={styles['featured-work__container']}>
				<div className={styles['featured-work__inner']}>
					<div className={styles['featured-work__title']}>{work.title}</div>
					<div className={styles['featured-work__data']}>{work.date}</div>
				</div>
			</div>
		</div>
	)
}

export default FeaturedWork
