/* eslint-disable no-return-assign */
import React from 'react'

import styles from './styles/_button.module.scss'

function Button (props: {text: string, className: string, userFunction: Function}) {
	// @ts-ignore
	const { text, className, userFunction } = props

	return (
		<button className={`${styles.button} ${className}`} onClick={() => userFunction()}>{text}</button>
	)	
}

export default Button
