import React, { useState, useRef } from 'react'
import FEATURED_WORKS from '../../../../../../constants/featuredWorks'
import Title from '../../../../../../components/title/Title'

import styles from './styles/_featured-works.module.scss'
import MainInfo from '../../../../../../components/mainInfo/MainInfo'
import Button from '../../../../../../components/button/Button'
import FeaturedWork from './components/featuredWork/FeaturedWork'
import IFeaturedWork from '../../../../../../interfaces/IFeaturedWork.interface'
import ModalWindow from './components/modalWindow/ModalWindow'

const partWorksAmount = 5

function FeaturedWorks () {
  const featuredWorks = [...FEATURED_WORKS.map((work, index) => {
	return <FeaturedWork key={index} index={index} work={work} />
  })]

  const [viewingWorksAmount, setViewingWorksAmount] = useState(partWorksAmount)
  const [isAllWorks, setIsAllWorks] = useState(false)
  const [showModal, setShowModal] = useState(false)
	
  function ShowFeaturedWorks () {
	return (
		<React.Fragment>
			{
				featuredWorks.slice(0, viewingWorksAmount).map((work) => {
					if (viewingWorksAmount >= featuredWorks.length) {
						setIsAllWorks(true)
					} else {
						setIsAllWorks(false)
					}
					return work
				})
			}
		</React.Fragment>
	)
  }
	
  function viewMoreWorks () {
	if (viewingWorksAmount < featuredWorks.length) {
		return setViewingWorksAmount(viewingWorksAmount + partWorksAmount)
	}
	setShowModal(true)  
  }
	
  function viewLessWorks () {
	if (viewingWorksAmount !== 0) {
		return setViewingWorksAmount(viewingWorksAmount - partWorksAmount)
	}
	setShowModal(true)
  }

	return (
	  <React.Fragment>
		<section className={styles['featured-works']} id="works">
			<div className={styles['featured-works__container']}>
				<div className={styles['featured-works__inner']}>
					<div className={styles['featured-works__description']}>
						<Title className="featured-works__description-title" text='Featured Works' />
						<MainInfo text={['Some of my recent work', 'In different roles and cases due to clients needs']} />
						<Title className="featured-works__description-data" text="2021-2022" />
					</div>
					<div className={styles['featured-works__list']}> 
						<ShowFeaturedWorks />
					</div>
					{ isAllWorks ? <p className={styles['featured-works__endMsg']}>You have seen all works!</p> : '' }
					<div className={styles['featured-works__buttons']}>
						<Button text='Show More Works' className={styles['featured-works__button']} userFunction={() => viewMoreWorks()} />	
						<Button text='Show Less Works' className={styles['featured-works__button']} userFunction={() => viewLessWorks()} />
					</div>  
				</div>	  
			</div>
		</section>
	  </React.Fragment>
  )
}

export default FeaturedWorks
