/* eslint-disable no-use-before-define */
import React, { useEffect, useRef, useState } from 'react'
import getClientRect from '../../utils/getClientRect'
import Separator from './components/separator/Separator'

import styles from './styles/_main-info.module.scss'

function MainInfo (props: {text: string[]}) {
	const { text } = props

	const mainInfoLineRef = useRef(null)

	return (
		<div className={styles['main-info']}>
			<div className={styles['main-info__inner']}>
				<div className={styles['main-info__list']} ref={mainInfoLineRef}>
					{text.map((el: string, index: number, array: string[]) => {
						const elementRef = useRef(null)
						const [separator, setSeparator] = useState(<></>)

						useEffect(() => {
							const element = elementRef.current as unknown as HTMLElement
							const elementParent = element.offsetParent as HTMLElement
							const elementClientRect = getClientRect(element, <Separator position={{
								right: '',
								bottom: ''
							}} /> as unknown as HTMLElement)

							console.log(elementClientRect, ' - current element')

							// @ts-ignore
							setSeparator(() => <Separator position={elementClientRect} />)
						}, [])

						return (
							<React.Fragment key={index}>
								<p className={styles['main-info__list-element']} key={index} ref={elementRef}>{el}</p>
								{separator}
							</React.Fragment>
						)
					})}
				</div>
			</div>
		</div>
	)
}

export default MainInfo
