/* eslint-disable no-return-assign */
function getClientRect (textElement: HTMLElement, separator: HTMLElement) {
	console.log(separator)
	function getHorizontalPosition (textElement: HTMLElement, separator: HTMLElement) {
		const separatorPosition = { left: textElement.offsetTop }
	
		function centerSeparator (separatorPosition: { left: number }) {
			const separatorWidth = separator.offsetWidth
			const textElementMargin = getComputedStyle(textElement).marginRight
			const separatorCenteredPosition = separatorPosition.left - (+textElementMargin - separatorWidth) / 2
			return separatorCenteredPosition
		}

		return centerSeparator(separatorPosition)
	}
	
	function getVerticalPosition (textElement: HTMLElement, separator: HTMLElement) {
		const separatorPosition = { top: textElement.offsetTop }
		
		function centerSeparator (separatorPosition: { top: number }) {
			const textElementHeight = textElement.offsetHeight
			const separatorHeight = separator.offsetHeight
			const separatorCenteredPosition = separatorPosition.top - (+textElementHeight - separatorHeight) / 2
			return separatorCenteredPosition
		}

		return centerSeparator(separatorPosition)
	}

	function concatAllPositions (textElement: HTMLElement, separator: HTMLElement) {
		const horizontalPosition = getHorizontalPosition(textElement, separator)
		const verticalPosition = getVerticalPosition(textElement, separator)

		return { left: horizontalPosition, top: verticalPosition }
	}

	try {
		const elementClientRect = concatAllPositions(textElement, separator)
		console.log(elementClientRect)
		return elementClientRect
	} catch (error) {
		console.log(error)
		return { right: 0, bottom: 0 }
	}
}

export default getClientRect
